import React, { useId, useContext, useState, useRef, useEffect } from "react";
import { Context as IdContext } from "../../context/IdContext";
import { useDrop } from "react-dnd";
import { Context as FormContext } from "../../context/FormContext";
import { Context } from "../../context/DatesContext";
import axios from "axios";
import { addYears, formatWithOptions } from "date-fns/fp";
import { es } from "date-fns/locale";
import {
  isAfter,
  isBefore,
  eachDayOfInterval,
  add,
  addDays,
  subDays,
  differenceInDays,
  isWeekend,
  areIntervalsOverlapping,
} from "date-fns";

import "./Grid.css";

import { Rented } from "./Rented";

export const GridCell = ({
  containerRef,
  reference,
  rented,
  className,
  index,
  w,
  formdate,
  onDClick,
  containerWidth,
  containerLeft,
  interval,
  day,
  room,
  cellRef,
  cellH,
}) => {
  const { state, removeByDragMsg } = useContext(Context);
  const {
    calendar: { intervalRentedArray, intervalData },
  } = state;

  const [{ isOver }, drop] = useDrop(
    () => ({
      accept: "RENTED",
      hover: (item, monitor) => {},

      drop: async (item, monitor) => {
        const newEnd = isBefore(day, new Date(item.rented.start))
          ? subDays(
              new Date(item.rented.start),
              differenceInDays(new Date(item.rented.end), day)
            )
          : isAfter(day, new Date(item.rented.end))
          ? addDays(
              new Date(item.rented.start),
              differenceInDays(day, new Date(item.rented.end))
            )
          : new Date(item.rented.end);

        const ws = new WebSocket(`ws://localhost:8000/ws`);
        ws.onopen = () => {
          const isOverlapping = intervalRentedArray.some((interval) => {
            return (
              areIntervalsOverlapping(
                { start: day, end: newEnd },
                { start: new Date(interval.end), end: new Date(interval.start) }
              ) &&
              interval.room.toString() === room.toString() &&
              interval.id !== item.rented.id
            );
          });

          ws.send(
            JSON.stringify({
              ...item.rented,
              id: item.rented.id,
              start: newEnd,
              room,
              end: day,
              price: "0",
              isOverlapping: isOverlapping,
            })
          );
        };

        ws.onmessage = async (msg) => {
          const { data } = JSON.parse(msg.data);
          const superData = JSON.parse(data);

          if (!superData.isOverlapping) {
            const res = await axios.put(
              `http://localhost:8000/rented/${item.rented.id}`,
              {
                id: item.rented.id,
                start: formatWithOptions({ locale: es }, "yyyy/MM/dd")(day),
                room: room.toString(),
                end: formatWithOptions({ locale: es }, "yyyy/MM/dd")(newEnd),
                name: item.rented.name,
                email: item.rented.email,
                phone: item.rented.phone,
                nights: item.rented.nights.toString(),
                price: item.rented.price,
              }
            );
          }
          ws.close();
        };
      },

      collect: (monitor) => {
        return {
          isOver: monitor.isOver(),
          canDrop: monitor.canDrop(),
        };
      },
    }),
    [[intervalData]]
  );

  return index !== interval - 1 ? (
    <div style={{}} ref={drop}>
      <div
        ref={cellRef}
        onDoubleClick={onDClick}
        className={className}
        style={
          isOver
            ? { backgroundColor: "blue" }
            : {
                backgroundColor: `${
                  isWeekend(day.formDate) ? "green" : "#ffffff"
                }`,
              }
        }
      >
        {/* <div>{formatWithOptions({ locale: es }, "yyyy/MM/dd")(day)}</div> */}
        {rented ? (
          <Rented
            cellH={cellH}
            containerRef={containerRef}
            containerLeft={containerLeft}
            containerWidth={containerWidth}
            index={index}
            id={rented.id}
            className={"draggable"}
            w={w}
            rented={rented}
          />
        ) : null}
      </div>
    </div>
  ) : (
    <div
      style={
        isOver
          ? { backgroundColor: "blue" }
          : {
              backgroundColor: `${
                isWeekend(day.formDate) ? "#8b2626" : "#ffffff"
              }`,
            }
      }
      ref={reference}
      className="grid2"
    />
  );
};
