import { areIntervalsOverlapping, format } from "date-fns";
import React, { useEffect, useState } from "react";
import {
  add,
  differenceInDays,
  isBefore,
  isAfter,
  isSameDay,
  addDays,
} from "date-fns";

import { InputForm } from "./NewForm";
import { useForm, Controller } from "react-hook-form";
import "./FormGridStyles.css";
import CloseIcon from "@mui/icons-material/Close"; // Import CloseIcon from Material-UI icons
import { is } from "date-fns/locale";

export const NewFormm = ({ reservation }) => {
  const isEditMode = !!reservation;

  // const handleCloseForm = () => {
  //   showFormMsg(false); // Close the form
  // };
  // you can also target specific fields by their names
  const {
    state: {
      calendar: { intervalRentedArray },
    },
  } = useContext(DatesContext);
  const { showFormMsg } = useContext(FormContext);
  const { intervalRented, userId } = useContext(FormContext).state;
  const { start, end, room } = intervalRented;
  const startDate = new Date(start);
  const formattedStartDate = startDate.toISOString().split("T")[0];
  const endDate = new Date(end);
  const formattedEndDate = endDate.toISOString().split("T")[0];
  console.log(startDate, endDate);
  console.log(formattedStartDate, formattedEndDate);

  const {
    handleSubmit,
    control,
    watch,
    setValue,
    setError,
    clearErrors,
    formState: { errors },
  } = useForm({
    defaultValues: {
      checkIn: isEditMode ? reservation.checkIn : formattedStartDate,
      checkOut: isEditMode ? reservation.checkOut : formattedEndDate,
      phone: isEditMode ? reservation.phone : "2222222",
      nights: isEditMode
        ? reservation.nights
        : differenceInDays(endDate, startDate),
      roomNumber: isEditMode ? reservation.roomNumber : room,
      name: isEditMode ? reservation.name : "1",
      email: isEditMode ? reservation.email : "gon@gmail.com",
      roomtype: isEditMode ? reservation.roomtype : "doble",
      price: isEditMode ? reservation.price : 1,
      precioTotal: isEditMode ? reservation.precioTotal : 1,
      comments: isEditMode ? reservation.comments : "what?",
      adelanto: isEditMode ? reservation.adelanto : 1,
      montoPendiente: isEditMode ? reservation.montoPendiente : 1,
      dni: isEditMode ? reservation.dni : 24916296,
      time: isEditMode
        ? reservation.time
        : new Date().toLocaleTimeString([], {
            hour: isEditMode ? reservation.phone : "2-digit",
            minute: isEditMode ? reservation.phone : "2-digit",
          }),

      nombre_recepcionista: isEditMode
        ? reservation.nombre_recepcionista
        : "gonzalo",
    },
  });

  const [
    adelanto,
    checkIn,
    checkOut,
    comments,

    dni,
    email,

    montoPendiente,
    name,
    nights,
    nombre_recepcionista,
    phone,
    precioTotal,
    price,
    roomNumber,

    time,
  ] = watch([
    "adelanto",
    "checkIn",
    "checkOut",
    "comments",
    "dni",
    "email",
    "empresa",
    "montoPendiente",
    "name",
    "nights",
    "nombre_recepcionista",
    "phone",
    "precioTotal",
    "price",
    "roomNumber",
    "time",
  ]);
  useEffect(() => {
    if (isEditMode) {
      // Set all values from reservation data
      Object.keys(reservation).forEach((key) => {
        setValue(key, reservation[key]);
      });
    }
  }, [isEditMode, reservation, setValue]);
  const handleEmailChange = (newValue) => {
    const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
    setValue("email", newValue);
    if (!emailRegex.test(newValue)) {
      setError("email", {
        type: "pattern",
        message: "Invalid email address",
      });
    } else {
      clearErrors("email");
    }
  };

  const handleNameChange = (newValue) => {
    setValue("time", newValue);
  };
  // Handle change function for the "nombre_recepcionista" field
  const handleNombreRecepcionistaChange = (newValue) => {
    setValue("nombre_recepcionista", newValue);
  };

  const [endNights, setEndNights] = useState({
    checkIn,
    checkOut,
    active: false,
  });
  const handleCheckInChange = (value) => {
    console.log(value);
    setValue("checkIn", value);
    const newNights = differenceInDays(
      new Date(watch("checkOut")),
      new Date(value)
    );
    setValue("nights", newNights);
    setEndNights({ ...endNights, checkIn: value, active: false });
  };

  const handleCheckOutChange = (value) => {
    setValue("checkOut", value);
    // Calculate new nights based on the new checkOut value and the current checkIn value
    const newNights = differenceInDays(
      new Date(value),
      new Date(watch("checkIn"))
    );
    setValue("nights", newNights); // Update the nights based on the new checkOut date
    setEndNights({ ...endNights, checkOut: value, active: false });
  };
  const handleTimeChange = (newValue) => {
    setValue("time", newValue);
  };
  const handleNightsChange = (nights) => {
    const nightsValue = parseInt(nights, 10); // Ensure the value is treated as a number
    if (nightsValue < 1) {
      setError("nights", {
        type: "valueError",
        message: "Number of nights must be at least 1",
      });
    } else {
      clearErrors("nights"); // Clear the error if the value is valid
      const newEndDate = add(new Date(watch("checkIn")), { days: nightsValue });
      const formattedEndDate = newEndDate.toISOString().split("T")[0];
      setValue("checkOut", formattedEndDate);
      setEndNights({ checkOut: formattedEndDate, active: true });
    }
  };
  const handleRoomChange = (value) => {
    const roomNumber = parseInt(value, 10); // Convert value to an integer
    if (roomNumber < 1 || roomNumber > 15) {
      setError("roomNumber", {
        type: "rangeError",
        message: "Room number must be between 1 and 15.",
      });
    } else {
      clearErrors("roomNumber");
      setValue("roomNumber", value); // Only set the value if it's within the valid range
    }
  };
  const formatPhoneInput = (input) => {
    const digits = input.replace(/\D/g, ""); // Remove non-digit characters
    if (digits.length > 6) {
      return `${digits.slice(0, 2)}-${digits.slice(2, 6)}-${digits.slice(6)}`;
    } else if (digits.length > 2) {
      return `${digits.slice(0, 2)}-${digits.slice(2)}`;
    }
    return digits;
  };
  const handlePhoneChange = (value) => {
    const formattedValue = formatPhoneInput(value);
    setValue("phone", formattedValue);
  };

  const handleChangePricePerNight = () => {
    const totalPrice = parseFloat(watch("precioTotal"));
    const nights = parseFloat(watch("nights"));
    if (nights > 0) {
      const pricePerNight = totalPrice / nights;
      setValue("price", pricePerNight);
    }
  };

  // useEffect to update values based on certain conditions
  useEffect(() => {
    const nameValue = watch("name");
    handleNombreRecepcionistaChange(nameValue);
  }, [watch, setValue]);
  useEffect(() => {
    handleChangePricePerNight();
  }, [watch("precioTotal"), watch("nights"), setValue]);

  const handleadelantoChange = (value) => {
    const adelanto = parseFloat(value || 0);
    const total = parseFloat(watch("precioTotal") || 0);
    const montoPendiente = total - adelanto;
    setValue("adelanto", adelanto); // Actualizar el valor de adelanto
    setValue("montoPendiente", montoPendiente);
  };

  useEffect(() => {
    const newPrice = parseFloat(watch("price"));
    const newNights = parseFloat(watch("nights"));
    const newprecioTotal = newPrice * newNights;
    setValue("precioTotal", newprecioTotal);

    // Recalculate montoPendiente right after updating precioTotal
    const adelanto = parseFloat(watch("adelanto") || 0);
    const montoPendiente = newprecioTotal - adelanto;
    setValue("montoPendiente", montoPendiente);
  }, [watch("price"), watch("nights"), watch("adelanto"), setValue]);

  // useEffect(() => {
  //   const roomNumber = watch("roomNumber");

  //   // Example validation: Check if roomNumber is within a specific range
  //   if (roomNumber < 1 || roomNumber > 100) {
  //     // Assuming rooms are numbered 1-100
  //     setError("roomNumber", {
  //       type: "rangeError",
  //       message: "Room number must be between 1 and 15.",
  //     });
  //   } else {
  //     clearErrors("roomNumber");
  //   }

  //   // Additional validations can be added here, such as checking for room availability
  // }, [watch("roomNumber"), setError, clearErrors]);

  useEffect(() => {
    const checkInDate = new Date(watch("checkIn"));
    const checkOutDate = new Date(watch("checkOut"));
    const nights = parseInt(watch("nights"), 10);
    const expectedCheckOutDate = add(checkInDate, { days: nights });

    if (
      isAfter(checkOutDate, checkInDate) &&
      isSameDay(expectedCheckOutDate, checkOutDate)
    ) {
      // Clear errors if the conditions are met
      clearErrors(["checkIn", "checkOut", "nights"]);
    } else {
      // Set specific error messages for each case
      if (!isAfter(checkOutDate, checkInDate)) {
        setError("checkIn", {
          type: "dateRange",
          message: "Check-in date must be before check-out date.",
        });
      }
      if (!isSameDay(expectedCheckOutDate, checkOutDate)) {
        setError("nights", {
          type: "dateMismatch",
          message: "Number of nights does not match the dates selected.",
        });
      }
    }
  }, [
    watch("checkIn"),
    watch("checkOut"),
    watch("nights"),
    setError,
    clearErrors,
  ]);
  const handleCommentsChange = (value) => {
    setValue("comments", value);
  };
  useEffect(() => {
    // This code will run whenever the 'comments' value changes
    const currentComments = watch("comments");

    // You can perform any operations you need with currentComments here

    // Example: Validate comment length or content
    if (currentComments.length > 500) {
      setError("comments", {
        type: "maxLength",
        message: "Comment is too long. Maximum length is 500 characters.",
      });
    } else {
      clearErrors("comments");
    }
  }, [watch("comments"), setError, clearErrors]);

  // const onSubmit = async (data) => {

  //   const { checkIn, checkOut, nights, name, email, phone, roomNumber } = data;
  //   const nightsDifference = differenceInDays(
  //     new Date(checkOut),
  //     new Date(checkIn)
  //   );
  //   // console.log(checkIn, checkOut);
  //   if (nightsDifference !== parseInt(nights, 10)) {
  //     setError("nights", {
  //       type: "dateMismatch",
  //       message: "Number of nights does not match the dates selected.",
  //     });
  //     return; // Prevent form submission if nights don't match
  //   }

  //   const isOverlapping = intervalRentedArray.some((interval) => {
  //     return (
  //       ((new Date(checkIn) < new Date(interval.end) &&
  //         new Date(checkOut) > new Date(interval.start)) ||
  //         (new Date(interval.start) < new Date(checkOut) &&
  //           new Date(interval.end) > new Date(checkIn))) &&
  //       `${interval.room}` === `${watch("roomNumber")}`
  //     );
  //   });

  //   if (!isOverlapping) {
  //     try {
  //       // const res = await axios.post(
  //       // `https://localhost:8000/create-reservation`,
  //       // {
  //       const res = await axios.post(`/create-reservation`, {
  //         name,
  //         email,
  //         phone,
  //         userId,
  //         room: `${roomNumber}`,
  //         start: checkIn,
  //         end: checkOut,
  //         isOverlapping,
  //         price,
  //         nights,
  //         comments,
  //         precioTotal,
  //         adelanto,
  //         montoPendiente,
  //         dni,
  //         nombre_recepcionista,
  //         comments,
  //       });
  //       console.log(res);
  //     } catch (error) {
  //       console.error(error); // Handle error response
  //     }
  //   } else {
  //     // Handle the case where there is an overlapping reservation
  //     console.log("There is an overlapping reservation.");
  //     // let convertedCheckin = checkIn.split("-").join("/");
  //     // let convertedChecOut = checkOut.split("-").join("/");
  //     setError("checkIn", {
  //       type: "overlap",
  //       message: "The selected dates overlap with an existing reservation.",
  //     });
  //   }
  //   showFormMsg(false);
  // };
  const onSubmit = async (data) => {
    console.log(data);
    const { checkIn, checkOut, roomNumber } = data;

    // Check for overlapping reservations
    // const isOverlapping = intervalRentedArray.some((interval) => {
    //   return (
    //     areIntervalsOverlapping(
    //       { start: new Date(checkIn), end: new Date(checkOut) },
    //       { start: new Date(interval.start), end: new Date(interval.end) }
    //     ) && `${interval.room}` === `${roomNumber}`
    //   );
    // });
    const isOverlapping = intervalRentedArray.some((interval) => {
      return (
        ((new Date(checkIn) < new Date(interval.end) &&
          new Date(checkOut) > new Date(interval.start)) ||
          (new Date(interval.start) < new Date(checkOut) &&
            new Date(interval.end) > new Date(checkIn))) &&
        `${interval.room}` === `${watch("roomNumber")}`
      );
    });
    if (isOverlapping) {
      setError("checkIn", {
        type: "overlap",
        message: "The selected dates overlap with an existing reservation.",
      });
      return; // Stop the submission if there is an overlap
    }

    // If not overlapping, proceed with POST or PUT request
    try {
      const endpoint = isEditMode
        ? `/update-reservation/${data.id}`
        : "/create-reservation";
      const method = isEditMode ? "put" : "post";

      const response = await axios({
        method: method,
        url: endpoint,
        data: {
          ...data,
          // room: `${roomNumber}`,
          start: checkIn,
          end: checkOut,
          room: `${roomNumber}`,
          isOverlapping,
          userId,
        },
      });

      console.log(response);
      showFormMsg(false); // Close form on successful submission
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <form onSubmit={handleSubmit(onSubmit)} className="formContainer">
      <button
        onClick={() => showFormMsg(false)}
        style={{
          position: "absolute",
          top: "20px",
          right: "20px",
          border: "none",
          background: "orange",
        }}
        aria-label="Close form"
      >
        <CloseIcon />
      </button>
      <div className="roomNumber">
        <InputForm
          control={control}
          name="roomNumber"
          type="number"
          // select={true}
          className="roomNumberInput"
          label="Nro de Habitacion"
          value={watch("roomNumber")}
          options={Array.from({ length: 15 }, (_, i) => ({
            value: i + 1,
            label: i + 1,
          }))}
          rules={{
            required: "Room number is required",
            min: { value: 1, message: "Invalid room number" },
            max: { value: 15, message: "Invalid room number" },
          }}
          handleChange={handleRoomChange}
        />
        <InputForm
          control={control}
          name="nights"
          type="number"
          style={{ gridColumn: 2, gridRow: 2 }}
          label="Number of Nights"
          className="numbernights"
          value={watch("nights")}
          rules={{
            min: { value: 1, message: "Number of nights must be at least 1" },
          }}
          handleChange={handleNightsChange}
        />

        <InputForm
          control={control}
          name="checkIn"
          className="checks"
          type="date"
          label="Check-In"
          value={format(new Date(watch("checkIn")), "yyyy-MM-dd")}
          handleChange={handleCheckInChange}
        />
        <InputForm
          control={control}
          name="checkOut"
          type="date"
          className="checks"
          label="Check-Out"
          value={format(new Date(watch("checkOut")), "yyyy-MM-dd")}
          handleChange={handleCheckOutChange}
        />
      </div>
      <div className="price">
        <InputForm
          control={control}
          name="price"
          type="number"
          className="priceInputs"
          label="Precio"
          value={watch("price")}
          rules={{
            required: "Price is required",
            min: { value: 0, message: "Price must be greater than 0" },
          }}
          handleChange={() => {}}
        />
        <InputForm
          control={control}
          name="precioTotal"
          type="number"
          className="priceInputs"
          label="PrecioTotal"
          value={watch("precioTotal")}
          rules={{
            required: "PrecioTotal is required",
            min: { value: 0, message: "Price must be greater than 0" },
          }}
          handleChange={handleChangePricePerNight}
        />
        <InputForm
          control={control}
          name="montoPendiente"
          type="number"
          className="priceInputs"
          label="Monto Pendiente"
          value={watch("montoPendiente")}
          readOnly
          handleChange={() => {}}
        />
        <InputForm
          control={control}
          name="adelanto"
          type="number"
          className="priceInputs"
          label="Adelanto"
          value={watch("adelanto")}
          handleChange={handleadelantoChange}
        />
      </div>

      <div className="personalInfo">
        <InputForm
          control={control}
          name="name"
          type="text"
          className="personalInput"
          label="Nombre"
          value={watch("name")}
          handleChange={handleNameChange}
        />

        <InputForm
          control={control}
          name="email"
          type="email"
          label="Email"
          className="personalInput"
          value={watch("email")}
          rules={{
            required: "Email is required",
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: "Invalid email address",
            },
          }}
          handleChange={handleEmailChange}
        />
        <InputForm
          control={control}
          name="phone"
          type="tel"
          label="Telefono"
          className="personalInput"
          value={watch("phone")}
          handleChange={handlePhoneChange}
        />
        <InputForm
          control={control}
          name="dni"
          className="personalInput"
          type="number"
          label="Dni"
          value={watch("dni")}
          handleChange={() => {}}
        />
      </div>

      <div className="comments">
        <InputForm
          control={control}
          name="comments"
          multiline={true}
          rows={3}
          type="textarea"
          label="Comments"
          className="commentsStyles"
          value={watch("comments")}
          rules={{
            maxLength: {
              value: 500,
              message: "Comentario muy largo, abreviar por favor",
            },
          }}
          handleChange={handleCommentsChange}
          // Set this to occupy the second column
        />
        <div className="submitButton">
          <input type="submit" className="closeButton" value="Ok" />
        </div>
      </div>
      <div className="submitButton">
        <InputForm
          control={control}
          name="time"
          type="time"
          className="personalInput"
          label="Horario"
          value={watch("time")}
          handleChange={handleTimeChange}
        />
        <InputForm
          control={control}
          name="nombre_recepcionista"
          type="text"
          className=""
          label="Nombre Recepcionista"
          value={watch("nombre_recepcionista")}
          handleChange={handleNombreRecepcionistaChange}
        />
      </div>
    </form>
  );
};
